  .logo-anim-container{
      position: relative;
  }

  .logo-anim-1{
    position: absolute;
    content: '';
    width: 300px;
    height: 300px;
    background: #bd688e;
    border-radius: 75px;
    z-index: -1;
    animation: 2s clockwise infinite;
    transform: translate(32%, 28%);
  }

  .logo-anim-2{
    position: absolute;
    content: '';
    width: 300px;
    height: 300px;
    border-radius: 75px;
    z-index: -1;
    background: #F3CE5E;
    animation: 1s counterclockwise infinite;
    transform: translate(-33%, 28%);
  }

  .logo-anim-3{
    position: absolute;
    content: '';
    width: 300px;
    height: 300px;
    background: #bd688e;
    border-radius: 75px;
    z-index: -2;
    animation: 3s clockwise infinite;
    transform: translate(32%, 28%);
  }
  
  @keyframes clockwise {
    0% {
      top: -5px;
      left: 0;
    }
    12% {
      top: -2px;
      left: 2px;
    }
    25% {
      top: 0;
      left: 5px;    
    }
    37% {
      top: 2px;
      left: 2px;
    }
    50% {
      top: 5px;
      left: 0;    
    }
    62% {
      top: 2px;
      left: -2px;
    }
    75% {
      top: 0;
      left: -5px;
    }
    87% {
      top: -2px;
      left: -2px;
    }
    100% {
      top: -5px;
      left: 0;    
    }
  }
  
  @keyframes counterclockwise {
    0% {
      top: -5px;
      right: 0;
    }
    12% {
      top: -2px;
      right: 2px;
    }
    25% {
      top: 0;
      right: 5px;    
    }
    37% {
      top: 2px;
      right: 2px;
    }
    50% {
      top: 5px;
      right: 0;    
    }
    62% {
      top: 2px;
      right: -2px;
    }
    75% {
      top: 0;
      right: -5px;
    }
    87% {
      top: -2px;
      right: -2px;
    }
    100% {
      top: -5px;
      right: 0;    
    }
  }