.fake-body{
  font-size: 100%;
  /* background-color: #814661 ; */
}

.parent-games-container{
  position: relative;
  margin-left: 25%;
  margin-right: 25%;
  /* border-style: solid; */
  height: 55vh;
}

.games-container{
  position: absolute;
  top: 25vh;
}

.games-container .col{
  height: 20vh;
  width: auto;
  text-align: center;
  font-size: 1.2em;
}

.credits{
  position: fixed;
  bottom: 2%;
  left: 2%;
  color: #F3CE5E;
}

.itchio-call{
  position: fixed;
  bottom: 2%;
  right: 2%;
  color: #F3CE5E;
}

.content-centered{
  height: 100%;
  width: 100%;
  display: flex;
  position: absolute;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.content-not-main-center{
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.game-info-container img{
  height: 10vmin;
  width: 10vmin;
}

.game-info-container{
  position: relative;
}

.game-info-container .col{
  height: 15vh;
  margin-top: 1.7vh;
  margin-bottom: 1.7vh;
  
}

.game-info-container .col .align-info{
  position: absolute;
  left: 7%;
}

.game-info-container p{
  text-align: left;
}

.game-info-container .col .btn{
  margin: 0.5vh;
}

#formGridCVV{
  width: 60%;
}

#formGridCVVLabel{
  position: relative;
  right: 25%;
}

.round-wrap{
  border-style: solid;
  border-radius: 5%;
  border-width: 1px;
  border-color: rgba(150, 150, 150, 0.4);
}

.round-wrap-overlay{
  opacity: 0;
  position: absolute;
  width: 10vmin;
  left: 50%;
  transform: translate(-50%);
  transition: .5s ease;
  color: white;
  background-color:#F3CE5E;
  border-radius: 5%;
  border-width: 1px;
  border-color: rgba(150, 150, 150, 0.4);
}

.round-wrap-container:hover .round-wrap-overlay{
  opacity: 1;
}


form.round-wrap{
  padding: 1.5vmin;
}

.name-fixed-width-account{
  width: 15vw;
  overflow: hidden;
}

.price-fixed-width-account{
  width: 7vw;
  overflow: hidden;
}

.sticky-title-account{
  position: fixed;
}

.overflow-list-account{
  height: 50vh;
}

.game-card .card-title{
  font-size: 12px;
  height: 25px;
}

.game-card img{
  height: 80px;
  width: 125px;
}

.logo-img{
  width: 50px;
  background-color: #F3CE5E;
  border-radius: 20%;
  border-style: solid;
  border-color: #bd688e;
  border-width: 4px;
}

.logo-start{
  margin: 100px;
  width: 300px;
  background-color: #F3CE5E;
  border-radius: 20%;
}

.style-btn-custom-1{
  background-color: #bd688e !important;
}

.style-btn-custom-2{
  background-color: #F3CE5E !important;
}

.style-text-custom-1{
  color: #bd688e !important;
}

.style-text-custom-2{
  color: #F3CE5E !important;
}

.itchio-text-weight{
  font-weight: 900;
}

.container-account-back:hover .overlay-account-back{
  opacity: 1;
}

.overlay-account-back{
  width: 186px;
  transition: .5s ease;
  color: white;
  background-color:#F3CE5E;
  border-radius: 7%;
  opacity: 0;
}

.img-portfolio{
  width: 4em;
  min-width: 200px; 
  height: 5em;
  min-height: 300px;
  border-radius: 10%;
  border-style: solid;
  border-width: 2px;
  border-color: #F3CE5E;
}

.title-portfolio{
  padding: 10px;
}

.description-portfolio{
  border-radius: 2%;
  border-style: solid;
  border-width: 1px;
  border-color:  #bd688e;
  /* color: white; */
  padding: 20px;
}


